.mainsite{
    background-color: #384e96;
    justify-content: center;
    overflow: hidden;
}
.img-text{
    text-align: center;
    margin: 0vh 45vh 0vh 45vh;
}
.img-text h1{
    font-family: 'Marko One';
    font-size: 90px;
    margin: 0%;   
    color: #e0e0d6;
}
.img-text p{
    font-family: 'Open Sans';
    color: #e0e0d6;
    font-size: x-small;
    margin: 0%;
}
.img-text button{   
border-color: #e0e0d6; 
background-color: #384e96;
color: #e0e0d6;
 font-size: medium;
 width: 150px;
 height: 40px;
}

.box{
  margin-top: 4vh;
  margin-bottom: 1vh;
  font-family: 'Cairo';
  padding: 1vh;
  border: solid 3px #3853a8;
  height: 10px;
}

.box p{
    color: #090808f9; 
    font-size: small;
    margin: 0px;
}
.box h2{
font-family: 'Marko One';
font-size: 30px;
color: rgb(46, 46, 140);
text-align: center;
margin: 0px;
width: 180px;
}
.box h3{
    font-family: 'Marko One';
    font-size: 30px;
    color: rgb(46, 46, 140);
    text-align: center;
    margin: 0px;
    margin-left: 3vh;
    }
.textbox6{
    margin: 0px;
    padding: 0px;
}
.locationpage button{
    width: 200px;
}
/* Responsive */
@media (max-width: 1800px) {
    #item3{display: none;}
    #item4{display: none;}
    .img-text { margin-top: 10vh;}
}
@media (max-width: 1350px) {
   .mainsite{zoom: 0.8;}
}
@media (max-width: 1050px) {
    .mainsite{zoom: 0.7;}
 }
 @media (max-width: 750px) {
    .mainsite{zoom: 0.6;}
 }