@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cairo&family=Marko+One&display=swap');

/* Home CSS */
  body{
    background-color: #e8e4d4;
    margin: 6vh;
    margin: 0px;
    padding: 0px;
  }
  html{ scroll-behavior: smooth; }

  /* Navbar */
  .navbar { margin: 5vh 5vh 0vh 5vh; }
  .navbar li { display: inline-block; }
  .navbar li a {
    font-size: 26px;
    padding: 14px 40px;
    text-decoration: none;
    color: #90908a;
    font-family: 'Cairo';
  }
  .navbar .Title {
    font-size: 26px;
    float: right;
    margin-right: 3vh;
    padding: 8px 20px;
    font-family: 'Open Sans';
    color: #e1e1e1;
    background-color: #060f30;
  }
  .x{ font-weight: bold; }
  .navbar li a:hover{
    text-decoration: underline;
    color: #000000b9;
  }
  .navbar .Title:hover{
    cursor: pointer;
    background-color: #03081a;
  }