  /* Slider */
  .slider {
    background-color: #4054ac;
    margin: 5vh 0vh 5vh 0vh;
    display: flex;
    justify-content: space-around;
  }
 
  .slide {
    margin: 2vh;
    background-color: #e8e4d4;
    border-radius: 10px;
    text-align: center;
    padding: 2vh;
  }
 
  .slide h1 {
    font-family: 'Marko One';
    color: #040c34;
  }
 
  .slider p {
    font-family: 'Open Sans';
    color: #595959;
  }
 
  /* Main */
   .container2{
    justify-content: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: auto;
   }
   .image {margin: 1vh;}
   .page{
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 6vh;
   }
   .artbox h1{ 
     font-family: 'Marko One';
     font-size: 70px;
     margin: 0px 20px 0px 20px;
     color: #3853a8;
   }
   .artbox p{  
    font-family: 'Open Sans';
    color: #595959; 
    font-size: 15px;
   }
   .button button{  
     color:#f0f0f0;
     background-color:#333;
     height: 40px;
   }
   .page2 h1{
   text-align: center;
   font-family: 'Marko One';
   font-size: 70px;
   margin: 10px 20px 10px 20px;
   color: #3853a8;
   }
 
   /* Boxes */
   .big-box{
     display: flex;
     flex-wrap: wrap;
     padding:2vh 15vh 0vh 20vh ;
     justify-content: center;
   }
   .about-box {
     font-family: 'Cairo';
     padding: 0vh 2vh 0vh 2vh;
     display: flex;
     position: relative;
     margin: 2vh;
     display: flex  ;
   }
   .icons2{ margin: 5vh ;}
   .text{
     margin-top: 2vh;
     font-family: 'Cairo';
     text-align: left;
   }
   .text h3{
     font-family: 'Marko One';
     color:#1e1828;
     font-size: 25px;
     padding: 0px;
     margin: 0;
     font-weight: 900;
   }
   .text p{
     font-family: 'Open Sans';
     color: #595959;
     width: 470px;
   }
   .p3{
     margin: 3vh;
     text-align: center;
     font-family: 'Open Sans';
   }
 
  /* Corner Style */
  .top-border,
  .bottom-border{
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 25%;
  }
  .top-border { top: 0; }
  .bottom-border { bottom: 0;}
  .top-border:after ,
  .top-border:before,
  .bottom-border:after,
  .bottom-border:before {
    content: '';
    width: 100%;
    height: 100%;
  }
  .top-border:after,
  .top-border:before {
    border-top: 4px solid #3853a8;
  }
  .bottom-border:after,
  .bottom-border:before {
    border-bottom: 4px solid #3853a8;
  }
  .top-border:before,
  .bottom-border:before {
    border-left: 4px solid #3853a8;
  }
  .top-border:after,
  .bottom-border:after {
    border-right: 4px solid #3853a8;
  }

  /* Responsive */ 
  @media (max-width: 1215px) {
    .container2{ zoom: 0.8; margin: auto; }
}
  @media (max-width: 500px) {
    .page{ flex-direction: column;}
    .image { margin: auto; margin-top: 7vh; }
}